const Game = require("./game");

class PowerUp {
  constructor(options) {
    this.id = options.id;
    this.x = options.x;
    this.y = options.y;
    this.width = 20;
    this.height = 20;
    this.context = options.context;
    this.game = options.game;
    this.x_speed = 8;
    this.y_speed = 0;
    this.canvasHeight = this.context.canvas.height;
    this.canvasWidth = this.context.canvas.width;
    let power = this.chooseRandomPowerUp();
    this.power = power[0];
    this.color = power[1];
  }

  render() {
    this.context.beginPath();
    this.context.fillStyle = this.color;
    this.context.fillRect(this.x, this.y, this.width, this.height );
  }

  chooseRandomPowerUp() {
    const powerUps = [
      {"name": "addBall", "color": "#FF0000" },
      {"name": "slowBalls", "color": "#00FF00" },
      {"name": "fastBalls", "color": "#008000" },
      {"name": "bigPaddle", "color": "#0000FF" },
      {"name": "smallPaddle", "color": "#00FFFF" },
      {"name": "removeBricks", "color": "#FFFF00" }
    ];

    let randNum = Math.floor(Math.random() * powerUps.length);
    return [ powerUps[randNum]["name"], powerUps[randNum]["color"] ];
  }

  activatePower() {
    switch (this.power) {
      case "addBall":
        return this.game.addBall();
      case "slowBalls":
        return this.game.powerSlowBalls();
      case "fastBalls":
        return this.game.powerFastBalls();
      case "bigPaddle":
        return this.game.player.increasePaddleSize();
      case "smallPaddle":
        return this.game.player.decreasePaddleSize();
      case "removeBricks":
        return this.game.powerDeleteBricks();
    }
  }


  update(paddle) {
    this.x += this.x_speed;
    this.y += this.y_speed;

     // right edge - off the screen
    if (this.x >= this.canvasWidth) { this.game.removePowerUp(this); }
    // power up hits the paddle
    if (
      this.x >= paddle.x &&
      this.x <= paddle.x + 10 &&
      this.y >= paddle.y &&
      this.y <= (paddle.y + paddle.height)
    ) {
      this.activatePower();
      this.game.removePowerUp(this);
    }
  }


}

module.exports = PowerUp;



